.drop__content ul li a {
  color: #fff;
  text-transform: uppercase;
  cursor: pointer;
  letter-spacing: 2px;
  border-right: 4px solid #e1e1e100;
  width: 100%;
  font-family: barlowCondensed;
  font-size: 16px;
  text-decoration: none;
  transition: border-right .5s;
  display: inline-block;
}

.drop__content ul li a:hover {
  border-right: 4px solid #e1e1e180;
  transition: border-right .5s;
}

@media (width >= 768px) {
  .drop__content ul li a:hover {
    border-bottom: 4px solid #e1e1e180;
    border-right: none;
  }
}

.drop__content ul li a:focus {
  border-right: 4px solid #e1e1e1;
}

@media (width >= 768px) {
  .drop__content ul li a:focus {
    border-bottom: 4px solid #e1e1e1;
    border-right: none;
  }

  .drop__content ul li a {
    border-bottom: 4px solid #e1e1e100;
